import React from 'react';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import './App.css';

import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import PortfolioPage from './pages/PortfolioPage';
import ContactPage from './pages/ContactPage';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: 'Fashion Brands',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },        
        { title: 'Portfolio', path: '/portfolio' },
        { title: 'Contact', path: '/contact' }
      ],
      home: {
        title: "",
        subTitle: 'WE ARE SCENIC',
        text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Architecto quibusdam possimus quasi porro! itaque'
      },
      about: {
        subTitle: 'ABOUT US',
        text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Architecto quibusdam possimus quasi porro! itaque'
      },
      portfolio: {
        subTitle: 'PORTFOLIO',
        text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Architecto quibusdam possimus quasi porro! itaque'
      },
      contact: {
        title: '',
        subTitle: 'CONTACT US'
      },
      
    }
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
        
        <header className="navheader">
        <nav className="nav">
          <div className="brand">
            <h3>SCENIC</h3>
          </div>
          <div className="pages">
              <Link className="pg" to="/agency-portfolio/">HOME</Link>
              <Link className="pg" to="/agency-portfolio/about">ABOUT</Link>
              <Link className="pg" to="/agency-portfolio/portfolio">PORTFOLIO</Link>
              <Link className="pg" to="/agency-portfolio/contact">CONTACT</Link>
            </div>
        </nav>
      </header>


          <Route path="/agency-portfolio/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} /> 
          <Route path="/agency-portfolio/about" render={() => <AboutPage subTitle={this.state.about.subTitle} text={this.state.about.text} />} /> 
          <Route path="/agency-portfolio/portfolio" render={() => <PortfolioPage subTitle={this.state.portfolio.subTitle} text={this.state.portfolio.text} />} /> 
          <Route path="/agency-portfolio/contact" render={() => <ContactPage title={this.state.contact.title} subTitle={this.state.contact.subTitle} />} />  

        </Container>
      </Router>
    );
  }
}
export default App;
