import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import CardDeck from 'react-bootstrap/CardDeck'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import port4 from '../assets/work4.jpg';
import port8 from '../assets/work8.jpg';
import port11 from '../assets/work11.jpg';

class Blog extends Component {
  render() {
    return (
        <Container fluid className='blog'>
            <Container className='bloghead'>
            <div>
                        <h5>Read the latest post</h5>
                        <h3>BLOG POSTS</h3>
            </div>
            <Row>
                <Col>
                    <CardDeck>

                          <Card>
                            <Card.Img variant="top" src={port4} alt="w4" />
                            <Card.Body className='blogcard'>
                              <Card.Title>Card title</Card.Title>
                              <Card.Text>
                                This is a wider card with supporting text below as a natural lead-in to
                                additional content. This content is a little bit longer.
                              </Card.Text>
                            </Card.Body>
                          </Card>

                          <Card>
                            <Card.Img variant="top" src={port8} alt="w8" />
                            <Card.Body className='blogcard'>
                              <Card.Title>Card title</Card.Title>
                              <Card.Text>
                                This card has supporting text below as a natural lead-in to additional
                                content.{' '}
                              </Card.Text>
                            </Card.Body>
                          </Card>

                          <Card>
                            <Card.Img variant="top" src={port11} alt="w11" />
                            <Card.Body className='blogcard'>
                              <Card.Title>Card title</Card.Title>
                              <Card.Text>
                                This is a wider card with supporting text below as a natural lead-in to
                                additional content. This card has even longer content than the first to
                                show that equal height action.
                              </Card.Text>
                            </Card.Body>
                          </Card>

                    </CardDeck>
                </Col>
            </Row>

            </Container>            
        </Container>
    )
  }

}
export default Blog;