import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import fb from '../assets/facebook.svg';  
import insta from '../assets/instagram.svg';  
import twt from '../assets/twitter.svg';  

class Footer extends Component {
  render() {
    return (
        <Container fluid className='footer'>
            <Container>
                <Row className='foot'>
                    <Col sm={3} className=''>
                        <ul className="ft">
                        <h4>Scenic Products</h4>
                            <li><a href="/" path='' rel="" >Web Design</a></li>
                            <li><a href="/" rel="" >Graphic Design</a></li>
                            <li><a href="/" rel="" >Web Development</a></li>
                        </ul>
                    </Col>
                    <Col sm={3} className=''>
                        <ul className="ft">
                        <h4>Company</h4>
                            <li><a href="/about" rel="" >About Us</a></li>
                            <li><a href="/portfilio" rel="" >Portfolio</a></li>
                            <li><a href="/contact" rel="" >Contact Us</a></li>
                        </ul>
                    </Col>
                    <Col sm={3} className=''>
                        <ul className="ft">
                        <h4>Support</h4>
                            <li><a href="/" rel="" >Support</a></li>
                            <li><a href="/" rel="" >privacy</a></li>
                            <li><a href="/" rel="" >Terms of Service</a></li>
                        </ul>
                    </Col>
                    <Col sm={3} className='socialicon'>
                        <h4>Contact Us</h4>
                          <a href="https://facebook.com/" target="_blank" rel="noopener noreferrer" >
                          <img src={fb} alt="Facebook" />
                          </a>

                          <a href="https://instagram.com/" target="_blank" rel="noopener noreferrer">
                          <img src={insta} alt="instagram" />
                          </a>

                          <a href="https://twitter.com/" target="_blank" rel="noopener noreferrer">
                          <img src={twt} alt="Twitter" />
                          </a>
                    </Col>
                </Row>
            </Container>
        </Container>
    )
  }

}
export default Footer;