import React from 'react';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

import Heading from '../components/Heading';
import Footer from '../components/Footer';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

class ContactPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            message: '',
            disabled: false,
            emailsent: '',
        }
    }

    handleChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        this.setState({
            disabled: true
        });
    }

    render() {
        return(
            <div>
                <Heading subTitle={this.props.subTitle} />

                <Container fluid className=''>
                    <Container>
                        <Row className='contact'>
                            <Col sm={6} xs={12} className='form'>
                                <Form onSubmit={this.handleSubmit}>
                                    <Form.Group>
                                        <Form.Label htmlFor="full-name">Full Name</Form.Label>
                                        <Form.Control id="full-name" name="name" type="text" value={this.state.name} onChange={this.handleChange} />
                                    </Form.Group>

                                    <Form.Group>
                                        <Form.Label htmlFor="full-name">Email</Form.Label>
                                        <Form.Control id="email" name="email" type="email" value={this.state.email} onChange={this.handleChange} />
                                    </Form.Group>

                                    <Form.Group>
                                        <Form.Label htmlFor="message">Message</Form.Label>
                                        <Form.Control id="message" name="message" as="textarea" rows="3" value={this.state.message} onChange={this.handleChange} />
                                    </Form.Group>

                                    <Button className="d-inline-block" variant="primary" type="submit" disabled={this.state.disabled}>
                                        Send
                                    </Button>

                                    {this.state.emailSend === true && <p className="d-inline success-msg">Email Send</p>}
                                    {this.state.emailSend === true && <p className="d-inline err-msg">Email Not Send</p>}

                                </Form>
                            </Col>
                            <Col sm={5} xs={12} className='address'>
                                    <h5>Address</h5>
                                    <p>203 Fake St.Mountain view, San Francisco, California, USA</p>
                                    
                                    <h5>Phone</h5>
                                    <p>+1232 3256 552</p>
                                    
                                    <h5>Email Address</h5>
                                    <p>example@gmail.com</p>
                            </Col>
                        </Row>
                   </Container>
                </Container>
                
                <Footer />
            </div>
         );
    }
}

export default ContactPage;